#main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  h2 {
    font-variant: small-caps;
    width: 100%;
  }
  .image {

    max-width: 500px;
    margin: auto 10px auto 10px;
    img {
      border-radius: 50%;
    }
  }
  div {
    margin: 10px;
    p {
      max-width: 500px;
    }
  }
}